import './Research.scss';
import { researchStore } from '../../stores/ResearchStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Input, Spin } from 'antd';
import { LoadingOutlined, EyeOutlined, HeartOutlined, StarOutlined } from '@ant-design/icons';
import { useHistory } from 'react-router';
import { LazyLoadImage } from 'react-lazy-load-image-component';


const { Search } = Input;
export const Research = observer(() => {
    useEffect(() => {
        researchStore.getData();
    }, []);
    let list: any = researchStore.list;
    let left = list.length % 4;
    for (let i = 0; i < left; i++) {
        list.push(undefined);
    }
    let history = useHistory();
    window.onscroll = function () {
        var scrollTop =
            document.documentElement.scrollTop || document.body.scrollTop;
        //变量windowHeight是可视区的高度
        var windowHeight =
            document.documentElement.clientHeight || document.body.clientHeight;
        //变量scrollHeight是滚动条的总高度
        var scrollHeight =
            document.documentElement.scrollHeight || document.body.scrollHeight;
        var footer = document.getElementsByClassName('footerWrapper')[0];
        //滚动条到底部的条件
        if (scrollTop + windowHeight + footer.clientHeight > scrollHeight) {
            researchStore.getData(true);
        }
    };



    return (
        <div className="researchWrapper">
            <div className="topNav">
                <div className="menu">
                    <div className="title" onClick={() => {
                        researchStore.changeType('');
                    }}>
                        All Posts
                    </div>
                    {
                        researchStore.categories.map((r) => {
                            return <div className="title" style={{ color: researchStore.type === r.id ? 'rgb(87, 78, 126)' : 'rgb(0, 0, 0)' }} onClick={() => {
                                researchStore.changeType(r.id);
                            }}>
                                {r.name}
                            </div>
                        })
                    }
                </div>
                <div className="search">
                    <Search placeholder="请输入关键词" onSearch={(v) => {
                        researchStore.search(v);
                    }} enterButton />
                </div>
            </div>

            <ul className="content">
                {researchStore.loading ? <div className="loading">
                    <Spin indicator={<LoadingOutlined style={{ fontSize: 40 }} />} />
                </div> : researchStore.list.map((item, idx) => {
                    if (!item) {
                        return <li className="item"></li>
                    }
                    return <li className="item" key={idx} onClick={() => {
                        window.scrollTo(0, 0);
                        history.push(`/researchDetail/${item.id}`)
                    }}>
                        <LazyLoadImage
                            alt={item.title}
                            effect="blur"
                            src={item.thumb} />
                        <div className="title">{item.title}</div>
                        <p className="abstract">{item.content}</p>
                        <div className="bottom">
                            <div className="left">
                                <EyeOutlined />
                                <div className="viewCount">{item.viewcount}</div>
                                <StarOutlined style={{ color: item.isfollow === 1 ? '#201453' : 'inherit' }} />
                                <HeartOutlined style={{ color: item.islike === 1 ? '#201453' : 'inherit' }} />
                            </div>
                            <div className="date">{item.createtime.split(" ")[0]}</div>
                        </div>
                    </li>
                })
                }
            </ul>
            {researchStore.loadingMore && <div className="loadmore">
                <Spin indicator={<LoadingOutlined style={{ fontSize: 30 }} />} />
            </div>}
        </div>
    )
});