import './Research.scss';
import { ResearchDetailStore } from '../../stores/ResearchDetailStore';
import { observer } from 'mobx-react';
import { useEffect } from 'react';
import { Loading } from '../../components/Loading';

let researchDetailStore: ResearchDetailStore = new ResearchDetailStore();
export const ResearchDetail = observer((props: any) => {
    let id = props.match.params.id;
    useEffect(() => {
        researchDetailStore.getData(id);


        
    }, [id]);
    if (researchDetailStore.loading) {
        return <Loading />
    }
    return (
        <div className="researchDetailWrapper">
            <div>
                <div className="tabs">
                    <img className="back" src={require('../../images/back.png').default} alt="" onClick={() => {
                        props.history.goBack()
                    }} />

                    <div className="items">
                        <div className="like">
                            <img src={require(`../../images/icon_like${researchDetailStore.detail?.islike == 1 ? '_s' : ''}.png`).default} alt="" onClick={() => {
                                researchDetailStore.like();
                            }} />
                            <div className="count">{researchDetailStore.detail?.likecount}</div>
                        </div>
                        <img className='follow' src={require(`../../images/follow${researchDetailStore.detail?.isfollow == 1 ? '_s' : ''}.png`).default} alt="" onClick={() => {
                            researchDetailStore.follow();
                        }} />
                    </div>
                </div>
                <div className="pdf" style={{height:researchDetailStore.pdfHeight+'px'}}>
                    <div id="viewerContainer" >
                        <div id="viewer" className="pdfViewer"></div>
                    </div>
                </div>
            </div>
        </div>
    )
});