import './Header.scss';
import { LoginRegisterModal } from '../Login/LoginModal';
import { observer } from 'mobx-react';
import { userStore } from '../../stores/UserStore';
import { Link } from '../../components/Link';
import { Profile } from '../Profile/Profile';

export const Headers = observer((props:any) =>{
    console.log("propspropsprops",props)
    return (
        <div className="header">
            <div className="wrapper">
                <div className="logo">
                    <div className="logoInfo">
                        <img src={require('../../images/logo.webp').default} alt="" />
                        <div>
                            <span className="name">OLI VIEW</span>
                            <span className="abstract">Analysis smart, dream big.</span>
                        </div>
                    </div>

                </div>
                <ul className="nav">
                    <li key="top">
                        <Link to={'/'}>
                            主页
                        </Link>
                    </li>

                    <li key="shehui">
                        <Link to={'./test'}>
                            会员资讯
                            <ul className="subList">
                                <li className="title">
                                    <Link to={'/news'}>
                                        傲利观点
                                    </Link>
                                </li>
                                <li className="title">
                                    <Link to={'/videos'}>
                                        傲利视讯
                                    </Link>
                                </li>
                                <li className="title">
                                    <Link to={'/research'}>
                                        傲利研报
                                    </Link>
                                </li>
                            </ul>
                        </Link>
                    </li>

                    <li key="guonei">
                        <Link to={'/subscribe'}>
                            会员订阅
                        </Link>
                    </li>

                    <li key="guoji">
                        <Link to={'/aboutUs'}>
                            关于我们
                        </Link>
                    </li>

                    <li key="yule">
                        {userStore.userInfo ?  <Profile />:<div>
                            <img className="login" src={require('../../images/login.svg').default} alt="" onClick={() => {
                                userStore.loginModalVisible = true;
                            }} />
                            <LoginRegisterModal visible={userStore.loginModalVisible} cancle={() => {userStore.loginModalVisible = false}} />
                        </div> 
                           
                        }

                    </li>
                </ul>

            </div>
        </div>
    )
})